/* src/app/shared/footer/footer.css */
.footer {
  background: var(--primary);
  color: var(--text-light);
  margin-top: auto;
  position: relative;
  overflow: hidden;
  border-top: 3px solid var(--accent);
}
.footer::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%23ffffff' fill-opacity='0.03'%3E%3Cpath d='M36 34v-4h-2v4h-4v2h4v4h2v-4h4v-2h-4zm0-30V0h-2v4h-4v2h4v4h2V6h4V4h-4zM6 34v-4H4v4H0v2h4v4h2v-4h4v-2H6zM6 4V0H4v4H0v2h4v4h2V6h4V4H6z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
  opacity: 0.5;
  pointer-events: none;
}
.container {
  position: relative;
  z-index: 1;
}
.footer-brand {
  padding-left: 1rem;
}
.footer-logo {
  height: 45px;
  width: auto;
  filter: brightness(0) invert(1);
}
.brand-title {
  font-size: 2rem;
  font-weight: 900;
  color: var(--text-light);
}
.footer-description {
  color: rgba(255, 255, 255, 0.85);
  line-height: 1.8;
  margin-bottom: 1.5rem;
  max-width: 350px;
}
.social-links {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}
.social-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: rgba(108, 141, 181, 0.2);
  color: var(--text-light);
  text-decoration: none;
  transition: all var(--transition-base);
  font-size: 1.1rem;
  border: 2px solid transparent;
}
.social-icon:hover {
  background: var(--accent);
  transform: translateY(-5px) scale(1.1);
  box-shadow: 0 5px 15px rgba(108, 141, 181, 0.4);
  border-color: var(--accent-light);
}
.footer-heading {
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--text-light);
  margin-bottom: 1.5rem;
  position: relative;
  padding-bottom: 0.75rem;
}
.footer-heading::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  width: 50px;
  height: 3px;
  background: var(--accent);
  border-radius: var(--radius-full);
}
.footer-links {
  list-style: none;
  padding: 0;
  margin: 0;
}
.footer-links li {
  margin-bottom: 0.75rem;
}
.footer-link {
  color: rgba(255, 255, 255, 0.85);
  text-decoration: none;
  transition: all var(--transition-base);
  display: inline-flex;
  align-items: center;
  font-weight: 500;
}
.footer-link i {
  font-size: 0.75rem;
  opacity: 0;
  transform: translateX(5px);
  transition: all var(--transition-base);
}
.footer-link:hover {
  color: var(--accent-light);
  transform: translateX(-5px);
}
.footer-link:hover i {
  opacity: 1;
  transform: translateX(0);
}
.footer-contact {
  list-style: none;
  padding: 0;
  margin: 0;
}
.contact-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1rem;
  color: rgba(255, 255, 255, 0.85);
}
.contact-item i {
  font-size: 1.25rem;
  color: var(--accent-light);
  min-width: 24px;
}
.contact-item a {
  color: rgba(255, 255, 255, 0.85);
  text-decoration: none;
  transition: color var(--transition-base);
}
.contact-item a:hover {
  color: var(--accent-light);
}
.footer-divider {
  margin: 2rem 0;
  border: none;
  height: 1px;
  background:
    linear-gradient(
      to left,
      transparent,
      rgba(108, 141, 181, 0.3),
      transparent);
}
.footer-bottom {
  padding: 1rem 0;
}
.copyright {
  color: rgba(255, 255, 255, 0.75);
  font-size: 0.95rem;
}
.footer-legal-links {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.75rem;
  flex-wrap: wrap;
}
.legal-link {
  color: rgba(255, 255, 255, 0.75);
  text-decoration: none;
  font-size: 0.9rem;
  transition: color var(--transition-base);
}
.legal-link:hover {
  color: var(--accent-light);
}
.separator {
  color: rgba(255, 255, 255, 0.3);
}
.scroll-to-top {
  position: fixed;
  bottom: 2rem;
  left: 2rem;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: var(--accent);
  color: var(--text-light);
  border: none;
  font-size: 1.25rem;
  cursor: pointer;
  box-shadow: var(--shadow-lg);
  transition: all var(--transition-base);
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
}
.scroll-to-top:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-xl);
  background: var(--accent-dark);
}
.scroll-to-top:active {
  transform: translateY(-2px);
}
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.footer {
  animation: fadeInUp 0.6s ease-out;
}
@media (max-width: 768px) {
  .footer-brand {
    text-align: center;
    padding-left: 0;
    margin-bottom: 2rem;
  }
  .footer-description {
    max-width: 100%;
  }
  .social-links {
    justify-content: center;
  }
  .footer-heading::after {
    right: 50%;
    transform: translateX(50%);
  }
  .footer-links,
  .footer-contact {
    text-align: center;
  }
  .footer-link {
    justify-content: center;
  }
  .contact-item {
    justify-content: center;
  }
  .footer-legal-links {
    flex-direction: column;
    gap: 0.5rem;
  }
  .separator {
    display: none;
  }
  .scroll-to-top {
    bottom: 1.5rem;
    left: 1.5rem;
    width: 45px;
    height: 45px;
    font-size: 1.1rem;
  }
}
